import axios from 'axios'
import { environment } from '../enviorments/enviorment'

const FileExchange = {}

FileExchange.getData = function () {
  return new Promise(function (resolve, reject) {
    axios({
      method: 'GET',
      url: environment.apiBase + '/FileExchange',
      dataType: 'json'
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        resolve(err)
      })
  })
}

FileExchange.getFileFromStorage = (fileName) => {
  return new Promise(function (resolve, reject) {
    axios.get(environment.apiBase + '/getFileFromStorage?fileName=' + fileName)
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

FileExchange.uploadToStorage = (file) => {
  const formData = new FormData()
  formData.append('archivo', file)
  return new Promise(function (resolve, reject) {
    axios.post(environment.apiBase + '/uploadFileToStorage', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

FileExchange.upload = function (file) {
  const formData = new FormData()
  formData.append('archivo', file)
  return new Promise(function (resolve, reject) {
    axios.post(environment.apiBase + '/uploadFile', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        resolve(err)
      })
  })
}

FileExchange.uploadGen = function (file, ruta) {
  const formData = new FormData()
  formData.append('archivo', file)
  return new Promise(function (resolve, reject) {
    axios.post(environment.apiBase + '/uploadFileGen', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        resolve(err)
      })
  })
}

FileExchange.deleteFile = function (ruta, file) {
  return new Promise(function (resolve, reject) {
    axios({
      method: 'POST',
      url: environment.apiBase + '/deleteFile',
      dataType: 'json',
      data: {
        ruta: ruta,
        archivo: file
      }
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        resolve(err)
      })
  })
}

export default FileExchange
